	module.exports = {"data":{"allPostTitles":{"edges":[{"node":{"frontmatter":{"title":"Uport Specs","index":0,"type":"content"},"fields":{"slug":"/readme"}}}]},"navCategories":{"edges":[{"node":{"fields":{"slug":"/readme"},"headings":[{"value":"uPort Specs","depth":1},{"value":"Identities","depth":2},{"value":"Identities created using the uPort Mobile App","depth":3},{"value":"Request Flows","depth":2},{"value":"More about request flows","depth":3},{"value":"Request and Response Transports","depth":2},{"value":"Request/Response Transports","depth":3},{"value":"Off-chain Messages","depth":2},{"value":"More about Off-chain Messages","depth":3},{"value":"On-chain Transactions","depth":2},{"value":"More about Signing Ethereum transactions","depth":3},{"value":"uPort PKI","depth":2},{"value":"More about the uPort PKI","depth":3}],"frontmatter":{"category":"reference","index":0}}},{"node":{"fields":{"slug":"/messages/privatechain"},"headings":[{"value":"Private Chain Provisioning Message","depth":1},{"value":"Attributes","depth":2}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/messages/index"},"headings":[{"value":"Off-chain Messages","depth":1},{"value":"JSON Web Token","depth":2},{"value":"Requirements","depth":3},{"value":"JOSE Header","depth":4},{"value":"Attributes","depth":4},{"value":"Signature Verification","depth":3},{"value":"Unsigned Requests","depth":2},{"value":"Standard Unsigned Parameters","depth":3},{"value":"Message types","depth":2}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/messages/sharereq"},"headings":[{"value":"Selective Disclosure Request","depth":1},{"value":"Attributes","depth":4},{"value":"Unsigned Requests","depth":2}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/messages/shareresp"},"headings":[{"value":"Selective Disclosure Response","depth":1},{"value":"Attributes","depth":4}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/messages/verification"},"headings":[{"value":"Verification","depth":1},{"value":"Attributes","depth":2},{"value":"Claims Best Practices","depth":2},{"value":"Use atomic claims","depth":3},{"value":"Bundling multiple claims as one","depth":3},{"value":"Claim Taxonomy","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/privatechain"},"headings":[{"value":"Private Chain Provisioning Flow","depth":1},{"value":"Requirements","depth":2},{"value":"Provisioning Methodology","depth":2},{"value":"Endpoint","depth":2},{"value":"Client Callback","depth":2},{"value":"Successful Response","depth":3},{"value":"Errors","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/selectivedisclosure"},"headings":[{"value":"Selective Disclosure Flow","depth":1},{"value":"Endpoint","depth":2},{"value":"Send Request","depth":2},{"value":"Client Callback","depth":2},{"value":"Successful Response","depth":3},{"value":"Errors","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/tx"},"headings":[{"value":"Ethereum Transaction Flow","depth":1},{"value":"Endpoint","depth":2},{"value":"Addresses","depth":2},{"value":"ERC 67","depth":2},{"value":"Client Callback","depth":2},{"value":"Successful Response","depth":3},{"value":"Errors","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/verification"},"headings":[{"value":"Send Verification Flow","depth":1},{"value":"Endpoint","depth":2},{"value":"Send Verifications","depth":2},{"value":"Client Callback","depth":2},{"value":"Successful Response","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/index"},"headings":[{"value":"Uport Request Flows","depth":1},{"value":"Specific Application flows","depth":2},{"value":"Errors","depth":2}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/rest-apis/fuel-server"},"headings":[{"value":"Transaction Fueling Server","depth":1},{"value":"API Description","depth":2},{"value":"Fuel Token","depth":3},{"value":"Fund address","depth":3},{"value":"Endpoint","depth":4},{"value":"Header","depth":4},{"value":"Body","depth":4},{"value":"Response","depth":4},{"value":"Response data","depth":4}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/rest-apis/relay-server"},"headings":[{"value":"Meta Transaction Relaying Server","depth":1},{"value":"API Description","depth":2},{"value":"Fuel Token","depth":3},{"value":"MetaSignedTx","depth":3},{"value":"Endpoint","depth":4},{"value":"Header","depth":4},{"value":"Body","depth":4},{"value":"Response","depth":4},{"value":"Response data","depth":4}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/transports/index"},"headings":[{"value":"Request/Response Transports","depth":1},{"value":"Requests","depth":2},{"value":"Open URL","depth":3},{"value":"QR Code","depth":3},{"value":"Push Notifications","depth":3},{"value":"Responses","depth":2},{"value":"Redirect callback type","depth":3},{"value":"HTTP post callback type","depth":3},{"value":"Messaging Server","depth":2},{"value":"Preparing callback URL","depth":3},{"value":"Listening for Response","depth":3},{"value":"Endpoint","depth":4},{"value":"Response","depth":4},{"value":"Response data","depth":4},{"value":"Cleanup","depth":3},{"value":"Endpoint","depth":4},{"value":"Response","depth":4},{"value":"Examples","depth":2},{"value":"Mobile Browser Transport","depth":3},{"value":"Desktop Browser Serverless Transport","depth":3},{"value":"Desktop Browser Server Backed Transport","depth":3},{"value":"Push Notification Transport","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/transports/push"},"headings":[{"value":"Push Notification Transport","depth":1},{"value":"Encrypting the request","depth":2},{"value":"Proper encoding of the request","depth":3},{"value":"Encryption of the request","depth":3},{"value":"Encoding the encrypted data","depth":3},{"value":"Sending the request","depth":2},{"value":"Push notification server","depth":3},{"value":"Endpoint","depth":4},{"value":"Headers","depth":4},{"value":"Body","depth":4},{"value":"Response","depth":4},{"value":"Response data","depth":4}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/pki/index"},"headings":[{"value":"uPort PKI","depth":1},{"value":"Purpose","depth":2},{"value":"Creating and Verifying a JWT","depth":2},{"value":"Identity Document","depth":2},{"value":" item","depth":3},{"value":" item","depth":3},{"value":" item","depth":3},{"value":" item","depth":3},{"value":"Verifying a signature","depth":2},{"value":"Resolving the Public Key for ","depth":3},{"value":"Resolving the Public Encryption Key for ","depth":3},{"value":"Registering an Identity Document","depth":2},{"value":"External Accounts (Key Pairs)","depth":3},{"value":"Smart Contract Accounts","depth":3},{"value":"uPort Mobile App Created Identities","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/pki/identitydocument"},"headings":[{"value":"Identity Document","depth":1},{"value":"Contents","depth":2},{"value":"Parameters","depth":3}],"frontmatter":{"category":"reference","index":null}}}]},"postBySlug":{"html":"<h1 id=\"meta-transaction-relaying-server\"><a href=\"#meta-transaction-relaying-server\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Meta Transaction Relaying Server</h1>\n<p>All transactions on Ethereum like networks are paid using transaction fees known as <code>gas</code>. To avoid the requirement for all end users to fund themselves with ETH on their respective networks we support <a href=\"https://medium.com/uport/making-uport-smart-contracts-smarter-part-3-fixing-user-experience-with-meta-transactions-105209ed43e0\">meta transactions</a> which allows a 3rd party transaction fueling service to fund and relay transactions without requiring pre-funding the users account.</p>\n<h2 id=\"api-description\"><a href=\"#api-description\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>API Description</h2>\n<h3 id=\"fuel-token\"><a href=\"#fuel-token\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Fuel Token</h3>\n<p>A JWT or similar Bearer token (see <a href=\"https://tools.ietf.org/html/rfc6750\">RFC 6750</a>) SHOULD be issued as part of the <a href=\"/flows/privatechain\">Private Chain Provisioning Flow</a>.</p>\n<p>Funding service SHOULD verify based on their own business rules that the signer of the transaction is allowed to use the Fuel Token.</p>\n<h3 id=\"metasignedtx\"><a href=\"#metasignedtx\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>MetaSignedTx</h3>\n<p>To create a meta transaction, you need the following parameters:</p>\n<ul>\n<li><code>txRelayAddress</code> address of the <a href=\"https://github.com/uport-project/uport-identity/blob/develop/contracts/TxRelay.sol#L28\">txRelay</a> contract</li>\n<li><code>whitelistOwner</code> (use <code>0x0000000000000000000000000000000000000000</code>)</li>\n<li><code>metaNonce</code> the <a href=\"https://github.com/uport-project/uport-identity/blob/develop/contracts/TxRelay.sol#L11\">nonce stored in the txRelay contract</a>. This is NOT the same as the Ethereum account nonce</li>\n<li><code>to</code> ethereum address of recipient of transaction</li>\n<li><code>data</code> the data field of the ethereum transaction</li>\n</ul>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\">metaTxInput <span class=\"token operator\">=</span> <span class=\"token string\">'1900'</span> <span class=\"token operator\">+</span> <span class=\"token function\">stripHexPrefix</span><span class=\"token punctuation\">(</span>txRelayAddress<span class=\"token punctuation\">)</span>\n                    <span class=\"token operator\">+</span> <span class=\"token function\">stripHexPrefix</span><span class=\"token punctuation\">(</span>whitelistOwner<span class=\"token punctuation\">)</span> <span class=\"token operator\">+</span> <span class=\"token function\">padTo64</span><span class=\"token punctuation\">(</span>metaNonce<span class=\"token punctuation\">)</span> <span class=\"token operator\">+</span> to <span class=\"token operator\">+</span> data\nsignature <span class=\"token operator\">=</span> <span class=\"token function\">secp256k1</span><span class=\"token punctuation\">(</span><span class=\"token function\">keccak256</span><span class=\"token punctuation\">(</span>metaTxInput<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<p>Create an unsigned wrapper transaction to the <a href=\"https://github.com/uport-project/uport-identity/blob/develop/contracts/TxRelay.sol#L28\"><code>relayMetaTx</code></a> function.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">let</span> wrapperTx <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token string\">'gasPrice'</span><span class=\"token punctuation\">:</span> raw_gasPrice<span class=\"token punctuation\">,</span> <span class=\"token comment\">// ignored</span>\n      <span class=\"token string\">'gasLimit'</span><span class=\"token punctuation\">:</span> raw_gasLimit<span class=\"token punctuation\">,</span> <span class=\"token comment\">// ignored</span>\n      <span class=\"token string\">'value'</span><span class=\"token punctuation\">:</span> <span class=\"token number\">0</span><span class=\"token punctuation\">,</span>\n      <span class=\"token string\">'to'</span><span class=\"token punctuation\">:</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>txRelayAddress\n    <span class=\"token punctuation\">}</span>\n<span class=\"token keyword\">let</span> rawMetaSignedTx <span class=\"token operator\">=</span> txutils<span class=\"token punctuation\">.</span><span class=\"token function\">functionTx</span><span class=\"token punctuation\">(</span>txRelayAbi<span class=\"token punctuation\">,</span> <span class=\"token string\">'relayMetaTx'</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">[</span>\n        signature<span class=\"token punctuation\">.</span>v<span class=\"token punctuation\">,</span>\n        <span class=\"token function\">addHexPrefix</span><span class=\"token punctuation\">(</span>Buffer<span class=\"token punctuation\">.</span><span class=\"token keyword\">from</span><span class=\"token punctuation\">(</span>signature<span class=\"token punctuation\">.</span>r<span class=\"token punctuation\">,</span> <span class=\"token string\">'base64'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token string\">'hex'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n        <span class=\"token function\">addHexPrefix</span><span class=\"token punctuation\">(</span>Buffer<span class=\"token punctuation\">.</span><span class=\"token keyword\">from</span><span class=\"token punctuation\">(</span>signature<span class=\"token punctuation\">.</span>s<span class=\"token punctuation\">,</span> <span class=\"token string\">'base64'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">toString</span><span class=\"token punctuation\">(</span><span class=\"token string\">'hex'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n        raw_to<span class=\"token punctuation\">,</span>\n        raw_data<span class=\"token punctuation\">,</span>\n        <span class=\"token function\">addHexPrefix</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>whitelistOwner<span class=\"token punctuation\">)</span>\n      <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> wrapperTx<span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<h4 id=\"endpoint\"><a href=\"#endpoint\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Endpoint</h4>\n<p><code>POST /api/v2/relay/</code></p>\n<h4 id=\"header\"><a href=\"#header\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Header</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code class=\"language-none\">Authorization: Bearer <jwt token></code></pre>\n      </div>\n<h4 id=\"body\"><a href=\"#body\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Body</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code class=\"language-none\">{\n  metaSignedTx: <raw signed meta Tx>,\n  blockchain: <blockchain id or name>,\n  metaNonce: <meta nonce>\n}</code></pre>\n      </div>\n<h4 id=\"response\"><a href=\"#response\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Response</h4>\n<table>\n<thead>\n<tr>\n<th align=\"center\">Status</th>\n<th>Message</th>\n<th></th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">200</td>\n<td>Ok.</td>\n<td>address funded</td>\n</tr>\n<tr>\n<td align=\"center\">403</td>\n<td>Forbidden</td>\n<td>JWT token missing or invalid</td>\n</tr>\n<tr>\n<td align=\"center\">404</td>\n<td>Not found</td>\n<td>Blockchain not found</td>\n</tr>\n<tr>\n<td align=\"center\">429</td>\n<td>Too many conns</td>\n<td>Too many connections</td>\n</tr>\n<tr>\n<td align=\"center\">500</td>\n<td>Internal Error</td>\n<td>Internal error</td>\n</tr>\n</tbody>\n</table>\n<h4 id=\"response-data\"><a href=\"#response-data\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Response data</h4>\n<p>The transaction hash of the real transaction on the blockchain:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code class=\"language-none\">{\n  txHash: <tx hash>\n}</code></pre>\n      </div>","timeToRead":2,"excerpt":"Meta Transaction Relaying Server All transactions on Ethereum like networks are paid using transaction fees known as  gas . To avoid the…","headings":[{"value":"Meta Transaction Relaying Server","depth":1},{"value":"API Description","depth":2},{"value":"Fuel Token","depth":3},{"value":"MetaSignedTx","depth":3},{"value":"Endpoint","depth":4},{"value":"Header","depth":4},{"value":"Body","depth":4},{"value":"Response","depth":4},{"value":"Response data","depth":4}],"frontmatter":{"title":"Meta Transaction Relaying Server","category":"reference","type":"content"},"fields":{"slug":"/rest-apis/relay-server"}},"postByCategory":{"totalCount":1,"edges":[{"node":{"fields":{"slug":"/readme"},"headings":[{"value":"uPort Specs","depth":1},{"value":"Identities","depth":2},{"value":"Identities created using the uPort Mobile App","depth":3},{"value":"Request Flows","depth":2},{"value":"More about request flows","depth":3},{"value":"Request and Response Transports","depth":2},{"value":"Request/Response Transports","depth":3},{"value":"Off-chain Messages","depth":2},{"value":"More about Off-chain Messages","depth":3},{"value":"On-chain Transactions","depth":2},{"value":"More about Signing Ethereum transactions","depth":3},{"value":"uPort PKI","depth":2},{"value":"More about the uPort PKI","depth":3}],"excerpt":"uPort Specs uPort is a platform for user centric identity and communication. The platform currently consists of our mobile app, Ethereum…","timeToRead":2,"frontmatter":{"title":"Uport Specs","category":"reference","index":0,"type":"content"}}}]}},"pathContext":{"slug":"/rest-apis/relay-server"}}

